import React, { Component } from 'react';
import interact from './../js/interact.js';
import './../css/Dash.css';
import 'bootstrap/dist/css/bootstrap.min.css';

class Dash extends Component {
  
  constructor() {
    super();
    this.state = {
      objects: {}
    }
  }

  componentWillMount() {
    this.setState({
      objects: JSON.parse(localStorage.getItem("objects"))
    });

    if(JSON.parse(localStorage.getItem("objects")) === null)
      window.location.href = "/";
  }

  componentDidMount() {
    interact('.draggable')
    .draggable({
      inertia: true,
      restrict: {
        restriction: document.getElementById('limit'),
        endOnly: true,
        elementRect: { top: 0, left: 0, bottom: 1, right: 1 }
      },
      autoScroll: true,
      onmove: dragMoveListener,
    });

    interact('.draggable-d')
    .draggable({
      inertia: true,
      restrict: {
        restriction: document.getElementById('limit'),
        endOnly: true,
        elementRect: { top: 0, left: 0, bottom: 1, right: 1 }
      },
      autoScroll: true,
      onmove: dragMoveListenerDiamond,
    });

    interact('.draggable-p')
    .draggable({
      inertia: true,
      restrict: {
        restriction: document.getElementById('limit'),
        endOnly: true,
        elementRect: { top: 0, left: 0, bottom: 1, right: 1 }
      },
      autoScroll: true,
      onmove: dragMoveListenerParallel,
    });

    function dragMoveListener (event) {
      var target = event.target,
          x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx,
          y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

      target.style.webkitTransform =
      target.style.transform =
        'translate(' + x + 'px, ' + y + 'px)';
      target.setAttribute('data-x', x);
      target.setAttribute('data-y', y);
    }

    function dragMoveListenerDiamond (event) {
      var target = event.target,
          x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx,
          y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

      target.style.webkitTransform =
      target.style.transform =
        'translate(' + x + 'px, ' + y + 'px)' + 'rotateZ(45deg)';
      target.setAttribute('data-x', x);
      target.setAttribute('data-y', y);
    }

    function dragMoveListenerParallel (event) {
      var target = event.target,
          x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx,
          y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

      target.style.webkitTransform =
      target.style.transform =
        'translate(' + x + 'px, ' + y + 'px)' + 'skew(-30deg)';
      target.setAttribute('data-x', x);
      target.setAttribute('data-y', y);
    }

    window.dragMoveListener = dragMoveListener;
    window.dragMoveListenerDiamond = dragMoveListenerDiamond;
    window.dragMoveListenerParallel = dragMoveListenerParallel;
  }

  render() {
    return (
      <div className="Home full-window">
        <div className="container">
          <div className="Dash__Instruction row">
            <div className="col-md-2 col-lg-2 col-xl-2"/>
            <div className="col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8">
              <h1>Instruction</h1>
              <h4>
                Given a number of flowchart pieces, your goal is to successfully construct a complete flowchart that corresponds to the series of steps in order to solve the given problem. 
                <br/>
                <br/>
                Problem: {this.state.objects.problem}
                <br/>
                <br/>
                Take note: This is a drag-and-drop platform. Build the flowchart by dragging and dropping the pieces needed. Good luck and have fun!
              </h4>
            </div>
            <div className="col-md-2 col-lg-2 col-xl-2"/>
          </div>
          <div className="Dash__Instruction row">
            <div className="col-md-1 col-lg-1 col-xl-1"/>
            <div className="col-xs-12 col-sm-12 col-md-10 col-lg-10 col-xl-10">
              <div className="row">
                <div className="Dash__Flowchart col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6">
                  <h3>Flowchart Field</h3>
                </div>
                <div className="Dash__Flowchart col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6">
                  <h3>Flowchart Pieces</h3>
                </div>            
              </div>
              <div id="limit" className="row">
                <div className="Dash__Flowchart col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6">
                  <div className="row">
                    <div className="col-xs-12 col-md-12 col-sm-12 col-lg-12 col-xl-12">
                    </div>
                  </div>
                  <div className="row">
                    <div className="Dash__Flowchart__Parent col-xs-12 col-md-12 col-sm-12 col-lg-12 col-xl-12">
                    </div>
                  </div>
                </div>
                <div className="Dash__Flowchart col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6">
                  <div className="row">
                    <div className="Dash__Flowchart__Parent col-xs-12 col-md-12 col-sm-12 col-lg-12 col-xl-12">

                      {
                        this.state.objects.types.map((obj) => {
                          if(obj.shape === "oval") {
                            return(
                              <div className="Dash__Flowchart__Entry draggable">
                                <p> {obj.content} </p>
                              </div>
                            )
                          }
                          else if(obj.shape === "rectangle") {
                            return(
                              <div className="Dash__Flowchart__Meth draggable">
                                <p> {obj.content} </p>
                              </div>
                            )
                          }
                          else if(obj.shape === "diamond") {
                            return(
                              <div className="Dash__Flowchart__Diamond draggable-d">
                                <p className="Dash__Flowchart__Diamond__Label"> {obj.content} </p>
                              </div>
                            )
                          }
                          else if(obj.shape === "paralellogram") {
                            return(
                              <div className="Dash__Flowchart__Parallel draggable-p">
                                <p className="Dash__Flowchart__Parallel__Label"> {obj.content} </p>
                              </div>
                            )
                          }
                          return(<span/>)
                        })               
                      }

                    </div>
                  </div>
                </div>
              </div>  
            </div>
            <div className="col-md-1 col-lg-1 col-xl-1"/>
          </div>
        </div>
      </div>
    );
  }
}

export default Dash;



