import React, { Component } from 'react';
import logo from './../assets/seal-bg-02.png';
import './../css/Home.css';
import 'bootstrap/dist/css/bootstrap.min.css';

class Home extends Component {

  constructor() {
    super();
    this.state = {
      objects: {

      }
    }
  }

  render() {
    return (
      <div className="vertical-center Home">
        <div className="container">
          <div className="row">
            <div className="col-md-2 col-lg-2 col-xl-2"/>
            <div className="col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8">
              <img src={logo} alt="acss-logo" className="Home__Logo"/>
            </div>
            <div className="col-md-2 col-lg-2 col-xl-2"/>
          </div>
          <div className="Home__Tag row">
            <div className="col-md-2 col-lg-2 col-xl-2"/>
            <div className="col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8">
              <h2>Flowchart Manager</h2>
              <h3>Alliance of Computer Science Students</h3>
            </div>
            <div className="col-md-2 col-lg-2 col-xl-2"/>
          </div>
          <div className="Home__ButtonPanel row">
            <div className="col-md-2 col-lg-2 col-xl-2"/>
            <div className="col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8">
              
              <input type="file" name="file" id="file" className="Home__Inputfile" onChange={(event)=> { 
                  var input = event.target;
                    var reader = new FileReader();
                    reader.onload = function(){
                      localStorage.setItem("objects", reader.result);
                      window.location.href = "#/dash";                    
                    };
                    reader.readAsText(input.files[0]);
                }}
                onClick={(event)=> { 
                  this.setState({
                    objects: {}
                  })
                }} />

              <label htmlFor="file">
                <a className="Home__Button">Upload Template</a>
              </label>
            </div>
            <div className="col-md-2 col-lg-2 col-xl-2"/>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;



